body {
  margin: 0;
  padding: 0;
  font-family: "Courier New" Courier,monospace;
   background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
}
.style1{
    border-radius: 2%;
    box-shadow: 2px 2px 2px 2px;
    margin-top: 50px;
    margin-left: 50px;
    width:450px;
}
img {
    width: 300px;
    height:400px;
    border-radius: 2%;
    margin-top: 50px;
    cursor: pointer;
}
.container{
  display: flex;
  justify-content: center;
 
}
.particles{
    position: fixed;
    top: 0;
    right:0;
    bottom: 0;
    left:0;
    z-index: -1;
}
.container2{
    display: flex;
    justify-content: space-between;
    width: 90%;
}
.flex-item2{
    text-align: center;
    font-size: 25px;
    font-weight: bold;
}
.color2{
    background: linear-gradient(89deg, #c740ac 0%, #2baebd 100%);
    box-shadow: 5px 5px 5px grey;
    color:black;
    font-size: 25px;
}
.flex{
    color:black;
}
@media only screen and ( max-width: 1280px){
    html{
        width: 1280px;
    }
 
    .container2{
     
        width:100%;
        display:flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}
@media only screen and (max-width: 640px){
   html{
       width: 640px;
       
   }

   .container2{
    
       width:100%;
       display:flex;
       flex-wrap: wrap;
       justify-content: center;
       
   }
   .flex{
       font-size: 35px;
   }
  
}
